"use client";

import { useRef, useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Calendar, MapPin, Users, ArrowRight, Loader2 } from "lucide-react";
import { format } from "date-fns";
import { createLocationSlug } from "@/lib/location-utils";
import Image from "next/image";
import { useSupabase } from "@/components/providers/supabase-provider";

import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Skeleton } from "@/components/ui/skeleton";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel";
import { CATEGORIES, getMockImage } from "@/lib/data";
import Autoplay from "embla-carousel-autoplay";
import EventCard from "@/components/event-card";

export default function ExplorePage() {
  const router = useRouter();
  const plugin = useRef(Autoplay({ delay: 2000, stopOnInteraction: true }));

  const { supabase } = useSupabase();
  const [featuredEvents, setFeaturedEvents] = useState([]);
  const [localEvents, setLocalEvents] = useState([]);
  const [popularEvents, setPopularEvents] = useState([]);
  const [categoryCounts, setCategoryCounts] = useState({});
  const [currentUser, setCurrentUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    async function loadData() {
      try {
        // 1. Get Current User (for location)
        const { data: { session } } = await supabase.auth.getSession();
        let city = "Gurugram";
        let state = "Haryana";

        if (session) {
          const { data: profile } = await supabase
            .from('profiles')
            .select('*')
            .eq('id', session.user.id)
            .single();

          if (profile) {
            setCurrentUser(profile);
            // city = profile.city || city;
            // state = profile.state || state;
          }
        }

        // 2. Fetch Events (Published/Active only)
        const { data: events, error } = await supabase
          .from('events')
          .select('*')
          .in('status', ['published', 'active'])
          .order('created_at', { ascending: false });

        if (error) throw error;

        // 3. Process Logic locally (Simulating backend sorts/limits for now)
        setFeaturedEvents(events.slice(0, 3));
        setPopularEvents(events.slice(0, 6));
        setLocalEvents(events.slice(0, 4)); // In reality, filter by city

        // 4. Calculate Category Counts
        const counts = events.reduce((acc, event) => {
          const cat = event.category || event.event_type;
          acc[cat] = (acc[cat] || 0) + 1;
          return acc;
        }, {});
        setCategoryCounts(counts);

      } catch (err) {
        console.error("Discovery load failed:", err);
      } finally {
        setIsLoading(false);
      }
    }
    loadData();
  }, [supabase]);

  const handleEventClick = (slug) => {
    router.push(`/events/${slug}`);
  };

  const handleCategoryClick = (categoryId) => {
    router.push(`/explore/${categoryId}`);
  };

  const handleViewLocalEvents = () => {
    const city = currentUser?.location?.city || "Gurugram";
    const state = currentUser?.location?.state || "Haryana";
    const slug = createLocationSlug(city, state);
    router.push(`/explore/${slug}`);
  };

  // Format categories with counts
  const categoriesWithCounts = CATEGORIES.map((cat) => ({
    ...cat,
    count: categoryCounts?.[cat.id] || 0,
  }));

  if (isLoading) {
    return (
      <div className="container mx-auto px-4 py-8">
        {/* Hero Skeleton */}
        <div className="pb-12 text-center space-y-4">
          <Skeleton className="h-16 w-3/4 md:w-1/2 mx-auto" />
          <Skeleton className="h-6 w-2/3 md:w-1/3 mx-auto" />
        </div>

        {/* Featured Carousel Skeleton */}
        <div className="mb-16">
          <Skeleton className="w-full h-[400px] rounded-xl" />
        </div>

        {/* Local Events Skeleton */}
        <div className="mb-16">
          <div className="flex justify-between items-end mb-6">
            <div className="space-y-2">
              <Skeleton className="h-10 w-48" />
              <Skeleton className="h-4 w-32" />
            </div>
            <Skeleton className="h-10 w-24" />
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            {[1, 2, 3, 4].map(i => (
              <div key={i} className="space-y-3">
                <Skeleton className="h-40 w-full rounded-lg" />
                <Skeleton className="h-4 w-3/4" />
                <Skeleton className="h-4 w-1/2" />
              </div>
            ))}
          </div>
        </div>

        {/* Categories Skeleton */}
        <div className="mb-16">
          <Skeleton className="h-10 w-64 mb-6" />
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            {[1, 2, 3, 4].map(i => (
              <Skeleton key={i} className="h-24 rounded-lg" />
            ))}
          </div>
        </div>
      </div>
    );
  }

  return (
    <>
      {/* Hero Title */}
      <div className="pb-12 text-center">
        <h1 className="text-5xl md:text-6xl font-bold mb-4">Discover Events</h1>
        <p className="text-lg text-muted-foreground max-w-3xl mx-auto">
          Explore featured events, find what&apos;s happening locally, or browse
          events everywhere
        </p>
      </div>

      {/* Featured Carousel */}
      {featuredEvents && featuredEvents.length > 0 && (
        <div className="mb-16">
          <Carousel
            plugins={[plugin.current]}
            className="w-full"
            onMouseEnter={plugin.current.stop}
            onMouseLeave={plugin.current.reset}
          >
            <CarouselContent>
              {featuredEvents.map((event) => {
                const displayImage = event.cover_image || "/hero_image.jpeg";
                const eventTitle = event.title;
                const eventDescription = event.description;
                const eventCity = "Gurugram"; // Fallback for seeds
                const eventState = "Haryana";
                const eventCountry = "India";
                const eventStartDate = event.start_date;
                const eventRegistrations = 0; // Default

                return (
                  <CarouselItem key={event.id}>
                    <div
                      className="relative h-[400px] rounded-xl overflow-hidden cursor-pointer"
                      onClick={() => handleEventClick(event.slug)}
                    >
                      <Image
                        src={displayImage}
                        alt={eventTitle}
                        fill
                        className="object-cover"
                        priority
                      />
                      <div className="absolute inset-0 bg-gradient-to-r from-black/60 to-black/30" />
                      <div className="relative h-full flex flex-col justify-end p-8 md:p-12">
                        <Badge className="w-fit mb-4" variant="secondary">
                          {eventCity}, {eventState || eventCountry}
                        </Badge>
                        <h2 className="text-3xl md:text-5xl font-bold mb-3 text-white">
                          {eventTitle}
                        </h2>
                        <p className="text-lg text-white/90 mb-4 max-w-2xl line-clamp-2">
                          {eventDescription}
                        </p>
                        <div className="flex items-center gap-4 text-white/80">
                          <div className="flex items-center gap-2">
                            <Calendar className="w-4 h-4" />
                            <span className="text-sm">
                              {format(eventStartDate, "PPP")}
                            </span>
                          </div>
                          <div className="flex items-center gap-2">
                            <MapPin className="w-4 h-4" />
                            <span className="text-sm">{eventCity}</span>
                          </div>
                          <div className="flex items-center gap-2">
                            <Users className="w-4 h-4" />
                            <span className="text-sm">
                              {eventRegistrations} registered
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </CarouselItem>
                );
              })}
            </CarouselContent>
            <CarouselPrevious className="left-4" />
            <CarouselNext className="right-4" />
          </Carousel>
        </div>
      )}

      {/* Local Events */}
      {localEvents && localEvents.length > 0 && (
        <div className="mb-16">
          <div className="flex items-center justify-between mb-6">
            <div>
              <h2 className="text-3xl font-bold mb-1">Events Near You</h2>
              <p className="text-muted-foreground">
                Happening in {currentUser?.location?.city || "your area"}
              </p>
            </div>
            <Button
              variant="outline"
              className="gap-2"
              onClick={handleViewLocalEvents}
            >
              View All <ArrowRight className="w-4 h-4" />
            </Button>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            {localEvents.map((event) => (
              <EventCard
                key={event.id}
                event={event}
                variant="compact"
                onClick={() => handleEventClick(event.slug)}
              />
            ))}
          </div>
        </div>
      )}

      <div className="mb-16">
        <h2 className="text-3xl font-bold mb-6">Browse by Category</h2>

        <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-4 gap-4">
          {categoriesWithCounts.filter(cat => cat.count > 0).map((category) => (
            <Card
              key={category.id}
              className="py-2 group cursor-pointer hover:shadow-lg transition-all hover:border-purple-500/50"
              onClick={() => handleCategoryClick(category.id)}
            >
              <CardContent className="px-3 sm:p-6 flex items-center gap-3">
                <div className="text-3xl sm:text-4xl">{category.icon}</div>
                <div className="flex-1 min-w-0">
                  <h3 className="font-semibold mb-1 group-hover:text-purple-400 transition-colors">
                    {category.label}
                  </h3>
                  <p className="text-sm text-muted-foreground">
                    {category.count} Event{category.count !== 1 ? "s" : ""}
                  </p>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>

      {/* Popular Events Across Country */}
      {popularEvents && popularEvents.length > 0 && (
        <div className="mb-16">
          <div className="mb-6">
            <h2 className="text-3xl font-bold mb-1">Popular Events</h2>
            <p className="text-muted-foreground">Trending events</p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {popularEvents.map((event) => (
              <EventCard
                key={event.id}
                event={event}
                variant="list"
                onClick={() => handleEventClick(event.slug)}
              />
            ))}
          </div>
        </div>
      )}

      {!isLoading &&
        (!featuredEvents || featuredEvents.length === 0) &&
        (!localEvents || localEvents.length === 0) &&
        (!popularEvents || popularEvents.length === 0) && (
          <Card className="p-12 text-center">
            <div className="max-w-md mx-auto space-y-4">
              <div className="text-6xl mb-4">ðŸŽ‰</div>
              <h2 className="text-2xl font-bold">No events yet</h2>
              <p className="text-muted-foreground">
                Be the first to create an event in your area!
              </p>
              <Button asChild className="gap-2">
                <a href="/create-event">Create Event</a>
              </Button>
            </div>
          </Card>
        )}
    </>
  );
}
